.container {
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px;
    min-width: 320px;
    width: 100%;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
    .container {
        width: 768px;
    }
}

@media screen and (min-width: 1680px) {
    .container {
        width: 1680px;
    }
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 1.55;
  letter-spacing: 0.08em;

  background-color: var(--white-color);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
